import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View, Panel, PanelHeader, Cell, Tabs, List, TabsItem, Group, ListItem, Alert, Avatar, ScreenSpinner, Epic, Tabbar, HeaderButton, TabbarItem, Div, Button } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import './css/my.css';
import Icon28Newsfeed from '@vkontakte/icons/dist/28/newsfeed'
import Icon28KeyboardBotsOutline from '@vkontakte/icons/dist/28/keyboard_bots_outline';
import Icon24Back from '@vkontakte/icons/dist/24/back'
class App extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			activePanel: 'home',
			fetchedUser: null,
			users: [],
			activeTab: "today",
			spinner: null,
			auth: false,
			activeStory: 'manage'
		};
		this.onStoryChange = this.onStoryChange.bind(this)
		this.closePopout = this.closePopout.bind(this)
		this.openChat = this.openChat.bind(this)
	}

	openChat(e, chat){
		this.setState({
			activePanel: 'currentChat',
			currentChat: chat,
			popout: <ScreenSpinner />
		})
		fetch('https://ivandanilov.pythonanywhere.com/chat_users/'+chat.chat_id)
		.then(response => response.json())
		.then(data => {
			console.log(data)
			this.setState({popout: null, currentUsers: data.items})
		})
	}

	onStoryChange (e) {
    this.setState({ activeStory: e.currentTarget.dataset.story, activePanel: "home" })
	}
	
	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					fetch('https://topbotbackend.herokuapp.com/')
					.then(response => response.json())
					.then(users => {
						users = users.items;
						var tmp =  users.sort((a,b) => a.message_today - b.message_today)
						tmp = tmp.reverse()
						this.setState({
							users: tmp.map((user) => {var tmp = user; tmp.score = tmp.message_today; return tmp;}),
							me: tmp.find(x => x.user_id === e.detail.data.id) ? 
							tmp.find(x => x.user_id === e.detail.data.id).message_today : 0,
							spinner: null
						})
					})
					.catch(err => {
						console.log(err)
						this.setState({spinner: null})
					})
					this.setState({ fetchedUser: e.detail.data });
					break;
					case 'VKWebAppUpdateConfig':
						let schemeAttribute = document.createAttribute('scheme');
						schemeAttribute.value = e.detail.data.scheme ? e.detail.data.scheme : 'client_light';
						document.body.attributes.setNamedItem(schemeAttribute);
					break;
					case 'VKWebAppAccessTokenReceived':
						this.setState({popout: <ScreenSpinner />})
						fetch("https://ivandanilov.pythonanywhere.com/admins")
						.then(response => response.json())
						.then(data => {
							var chat_arr = data.items.map((chat, i) => {
								if(chat.user_id === this.state.fetchedUser.id){
									return {chat_id: chat.chat_id, lvl: chat.level}
								}
							})
							chat_arr = chat_arr.filter(function (el) {
								return el != null;
							});
							console.log(chat_arr)
							fetch("https://ivandanilov.pythonanywhere.com/chats")
							.then(response => response.json())
							.then(data => {
								var chats = [];
								for(var i = 0; i < chat_arr.length; i++){
									for(var j = 0; j < data.items.length; j++){
										if(data.items[j].chat_id === chat_arr[i].chat_id){
											var obj = data.items[j];
											obj.level = chat_arr[i].lvl;
											chats.push(obj)
										}
									}
									if(i === chat_arr.length-1){
										this.setState({
											popout: null,
											chats: chats,
											auth: true
										})
									}
								}
							})
						})
						break;
					case 'VKWebAppAccessTokenFailed':
						this.setState({ popout:
							<Alert
								actions={[{
									title: '',
									autoclose: true,
									style: 'cancel'
								}]}
								onClose={this.closePopout}
							>
								<h2>  </h2>
								<p> .   .</p>
							</Alert>
						});
						break;
				default:
			}
		});
		this.setState({spinner: <ScreenSpinner />})
		connect.send('VKWebAppGetUserInfo', {});
	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};

	closePopout(){
		this.setState({popout: null})
	}

	change = (e) => {
		switch(e.currentTarget.id){
			case 'today':
				var users = this.state.users;
				var tmp =  users.sort((a,b) => a.message_today - b.message_today)
				tmp = tmp.reverse()
				this.setState({
					users: tmp.map((user) => {var tmp = user; tmp.score = tmp.message_today; return tmp;}),
					activeTab: "today",
					me: tmp.find(x => x.user_id === this.state.fetchedUser.id) ? 
						tmp.find(x => x.user_id === this.state.fetchedUser.id).message_today : 0
				})
				break
			case 'week':
				var users = this.state.users;
				var tmp =  users.sort((a,b) => a.message_week - b.message_week)
				tmp = tmp.reverse()
				this.setState({
					users: tmp.map((user) => {var tmp = user; tmp.score = tmp.message_week; return tmp;}),
					activeTab: "week",
					me: tmp.find(x => x.user_id === this.state.fetchedUser.id) ? 
						tmp.find(x => x.user_id === this.state.fetchedUser.id).message_week : 0
				})
				break;
			case 'month':
				var users = this.state.users;
				var tmp =  users.sort((a,b) => a.message_month - b.message_month)
				tmp = tmp.reverse()
				this.setState({
					users: tmp.map((user) => {var tmp = user; tmp.score = tmp.message_month; return tmp;}),
					activeTab: "month",
					me: tmp.find(x => x.user_id === this.state.fetchedUser.id) ? 
						tmp.find(x => x.user_id === this.state.fetchedUser.id).message_month : 0
				})
				break;
			case 'year':
				var users = this.state.users;
				var tmp =  users.sort((a,b) => a.message_year - b.message_year)
				tmp = tmp.reverse()
				this.setState({
					users: tmp.map((user) => {var tmp = user; tmp.score = tmp.message_year; return tmp;}),
					activeTab: "year",
					me: tmp.find(x => x.user_id === this.state.fetchedUser.user_id) ? 
						tmp.find(x => x.user_id === this.state.fetchedUser.id).message_year : 0
				})
				break;	
			default:
		}
	}

	kick(chat_id, user_id, uindex = null) {
		fetch("https://ivandanilov.pythonanywhere.com/chats/"+chat_id+"/kick/?token=08116a4ffc159507ad8f09ddd38ae723eb9ca581fc4d339dfe8cb6cc9328e153091bfe5a9032ec8ce7128&user_ids="+user_id)
		.then(() => {
			if (uindex !== null) {
				let users = this.state.currentUsers;
				users.splice(uindex, 1);
				this.setState({
					currentUsers: users
				})
				this.setState({ popout: 
					<Alert 
					actions={[{ 
					title: '', 
					autoclose: true, 
					style: 'cancel' 
					}]} 
					onClose={this.closePopout} 
					> 
					   
					</Alert> 
					});
			}
		})
	}
	ban(chat_id, user_id, uindex = null) {
		fetch("https://ivandanilov.pythonanywhere.com/chats/"+chat_id+"/ban/?token=08116a4ffc159507ad8f09ddd38ae723eb9ca581fc4d339dfe8cb6cc9328e153091bfe5a9032ec8ce7128&user_ids="+user_id)
		.then(() => {
			if (uindex !== null) {
				let users = this.state.currentUsers;
				users.splice(uindex, 1);
				this.setState({
					currentUsers: users
				})
				this.setState({ popout: 
					<Alert 
					actions={[{ 
					title: '', 
					autoclose: true, 
					style: 'cancel' 
					}]} 
					onClose={this.closePopout} 
					> 
					   
					</Alert> 
					});
			}
		})
	}
	auth(){
		connect.send("VKWebAppGetAuthToken", {"app_id": 6973826, "scope": "status"});
	}
	render() {
		let fetchedUser = this.state.fetchedUser
		return (
			<Epic activeStory={this.state.activeStory} tabbar={
        <Tabbar>
          <TabbarItem
            onClick={this.onStoryChange}
            selected={this.state.activeStory === 'manage'}
            data-story="manage"
            text=""
					><Icon28KeyboardBotsOutline /></TabbarItem>
          <TabbarItem
            onClick={this.onStoryChange}
            selected={this.state.activeStory === 'top'}
            data-story="top"
						text=""
					><Icon28Newsfeed /></TabbarItem>
			</Tabbar>}>
			<View id="top" activePanel={this.state.activePanel} popout={this.state.spinner}>
          <Panel id="home">
            <PanelHeader> </PanelHeader>
            <Group>
              <Tabs theme="light">
                <TabsItem
				  onClick={this.change}
				  id="today"
                  selected={this.state.activeTab === 'today'}
                >
                  
                </TabsItem>
                <TabsItem
				  onClick={this.change}
				  id="week"
                  selected={this.state.activeTab === 'week'}
                >
					
                </TabsItem>
				<TabsItem
				  onClick={this.change}
				  id="month"
                  selected={this.state.activeTab === 'month'}
                >
                  
                </TabsItem>
				<TabsItem
				  onClick={this.change}
				  id="year"
                  selected={this.state.activeTab === 'year'}
                >
                  
                </TabsItem>
              </Tabs>
            </Group>
			{fetchedUser &&
			<Group>
				<ListItem
					before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200}/> : null}
					description={fetchedUser.city && fetchedUser.city.title ? fetchedUser.city.title : ''}
					indicator={this.state.me}
				>
					{`${fetchedUser.first_name} ${fetchedUser.last_name}`}
				</ListItem>
			</Group>}
			<Group>
              <List>
				  {
					  this.state.users.map((user,i) => 
					 	<Cell
						 	key={i}
							before={user.photo_200 ? <Avatar src={user.photo_200}/> : <Avatar src="https://vk.com/images/camera_200.png?ava=1" />} 
							indicator={user.score}
						>
							<div style={{display: 'flex'}}>
							{i+1}. {user.first_name+" "+user.last_name}
							</div>
						</Cell> 
					  )
				  }
              </List>
            </Group>
          </Panel>
        </View>
				<View id="manage" activePanel={this.state.activePanel} popout={this.state.popout}>
					<Panel id="home">
						<PanelHeader></PanelHeader>
						{
							!this.state.auth ? 
							<Group>
								<Div>
									      .
								</Div>
								<Div>
									<Button size="xl" level="commerce" onClick={this.auth}></Button>
								</Div>
							</Group> : 
							<Group>
										<Div style={{display:'flex',justifyContent:'center'}}>
										<a href="https://vk.com/app6441755_-173243972" style={{color: '#fff', textDecoration: 'none'}}><Button>  </Button></a>
										</Div>
								<List>
									{
										this.state.chats && this.state.chats.map((chat, i) => 
											<Cell 
												key={i}
												expandable
												id={chat}
												onClick={(e) => this.openChat(e,chat)}
												before={<Avatar src={chat.photo} />}
											>{chat.title}</Cell>
										)
									}
									{
										this.state.chats && this.state.chats.length === 0 ? <Div>       </Div> : null
									}
									
								</List>
							</Group>
						}
					</Panel>
					<Panel id="currentChat">
						<PanelHeader left={<HeaderButton onClick={() => this.setState({activePanel: "home"})}><Icon24Back/></HeaderButton>}> </PanelHeader>
						{
							this.state.currentChat && 
							<div>
								<Group>
									<Div>
										    : <b>{this.state.currentChat.level}</b>
									</Div>
								</Group>
								<Group>
									<List>
										{
											this.state.currentUsers && this.state.currentUsers.map((user, i) => 
												<Div>
													<Cell before={<Avatar src={user.photo_100} />} indicator = {user.level + " "}>
														{user.first_name+" "+user.last_name}
													</Cell>
													{
														user.level < this.state.currentChat.level &&
														<Div style={{marginLeft: "5px", marginRight: "5px", display: 'flex'}}>
														<Button style={{backgroundColor: "orange"}} className="btn-margin" stretched size="l" onClick = {()=>this.kick(this.state.currentChat.chat_id, user.id, i)}></Button>
														<Button style={{backgroundColor: "tomato"}} className="btn-margin" stretched size="l" onClick = {()=>this.ban(this.state.currentChat.chat_id, user.id, i)}></Button>
														</Div>
													}
												</Div>
												
											)
										}
									</List>
								</Group>
							</div>
						}
					</Panel>
				</View>
			</Epic>
		);
	}
}

export default App;



